body, html {
    font-size: 20px; 
    @media screen and (min-width: 1200px) {
        font-size: 30px; 
    }
} 
body {
    background: #ffe101 !important;
    color: #000;
    font-family: 'Syne', sans-serif;
    font-feature-settings: "kern", "liga", "onum";
    -webkit-font-smoothing: antialiased; 
    line-height: 170%;  
    padding: 100px 40px;
    text-transform: uppercase;
    @media screen and (min-width:1200px) { 
        padding: 200px 0;
    }
} 

img { 
    max-width:100%;
    height:auto;
    margin: 100px 0;
    opacity: .8;
    @media screen and (min-width:1200px) { 
        margin: 200px 0 100px 0;
    }
}

header {
    text-align: center; 
}

h1, h2, h3, h4, h5, h6 {
    font-weight: 600;
    font-family: 'Syne', sans-serif; 
}


h1 { 
    @media screen and (min-width:1200px) { 
        font-size: 4rem;
        text-align: center;  
    }
}

a {
    color: rgb(0, 81, 255);
    font-weight: 600;
    text-decoration: none;
}

section, footer {
    text-align: center;
    margin: 0 auto;
    max-width: 700px;
    @media screen and (min-width:1200px) {  
        margin: 0 auto; 
    }
}

p {

    text-align: justify;
}

h2 {  
    margin: 100px 0;
}

ul {
    list-style-type: none; 
    padding: 0;
    margin: 0; 
}

footer { 
    margin-top: 100px;
    @media scren and (min-width:1200px) { 
    font-size: 50%;
    }
    p { 
        text-align: center;
    }
}